'use client'
import { useEffect, useState } from 'react';
import {CardArea} from '@/components/cardarea';
import { AssuntoType } from '@/types/assuntotype';
import { Assuntos } from '@/data/assuntos';

export default function Home() {

      const [ today, setToday ] = useState<AssuntoType[]>([]);
      const [ tomorrow, setTomorrow ] = useState<AssuntoType[]>([]);
      const [ afterTomorrow, setAfterTomorrow ] = useState<AssuntoType[]>([]);
      const [ nextDays, setNextDays ] = useState<AssuntoType[]>([]);
  
      const hoje = new Date();
      const amanha = hoje.getDate() + 1;
      const depoisAmanha = hoje.getDate() + 2;
      const proximosDias = hoje.getDate() + 3;
  
      useEffect(() => { 
          Assuntos.forEach((assunto) => {
      if(assunto.dataAgendada.getDate() === hoje.getDate()){
          setToday(today => [...today, assunto]);
      } else if(assunto.dataAgendada.getDate() === amanha){
          setTomorrow(tomorrow => [...tomorrow, assunto]);
      } else  if(assunto.dataAgendada.getDate() === depoisAmanha){
          setAfterTomorrow(afterTomorrow => [...afterTomorrow, assunto])
      } else if(assunto.dataAgendada.getDate() >= proximosDias){
          setNextDays(nextDays => [...nextDays, assunto])
      }
          });
      }, [])
  
  return (
    <div className="w-screen h-screen flex overflow-hidden">
      <div className="h-screen w-[300px] bg-zinc-800">

      </div>                                               {/* O min-w-0 permite que um item (flex-1) encolha para caber no layout, mesmo que o conteúdo dentro dele seja muito mais largo. */}
      <div className="h-screen w-[100%] flex-1 bg-zinc-900 min-w-0">
        <div className="h-[10%] flex items-center px-4">
          <h2 className="text-3xl">Estudos</h2>
        </div>                      
         <div className=" w-full h-[90%] overflow-y-auto no-scrollbar">
            <CardArea title='Hoje'code={1}/>
            <CardArea title='Amanhã' code={2}/>
            <CardArea title='Depois de amanhã' code={3}/>
            <CardArea title='Próximos dias' code={4}/>
        </div>
      </div>
    </div>
  );
}
